    --transition-default: all 0.2s cubic-bezier( 0.22, 1, 0.36, 1 );
    --transition-fast: all 0.05s cubic-bezier( 0.17, 0.67, 0.83, 0.67 );
}

:root .theme--dark {
    --color-background-default: #191919;
    --color-text-light: #fff;
    --color-text-dark: #000;
    --color-text-default: rgba( 255, 255, 255, 0.7 );
    --color-card: var( --color-gray-dark );
}

:root .theme--light {
    --color-background-default: #f5f7f9;
    --color-text-light: #222;
    --color-text-dark: #fff;
    --color-text-default: rgba( 0, 0, 0, 0.7 );
    --color-card: #ffffff;
}

.color {
}

.theme--dark {
    background: var( --color-background-default );
}

.theme--light {
    background: var( --color-background-default );
}

.theme,
.theme a {
    color: var( --color-text-default );
}

.color--sidebar {
    background: var( --color-theme-2 );
}

.container {
    width: 100%;
    height: 100%;
    margin: 0 auto;
    overflow: auto;
}

.container--column {
    display: flex;
    flex-direction: column;
}

.container--maxwidth-600 {
    max-width: 600px;
}

.l-col--1of2 {
    width: 50%;
}

.main {
    width: 100%;
}

main.main {
    transition: var( --transition-fast );
}

.linkgroup {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.linkgroup__col1 {
    /* flex-shrink: 0; */
}

.linkgroup__col2 {
    display: flex;
    flex-grow: 1;
    display: block;
}

.linkgroup__name {
    margin: 60px 20px 20px 20px;
    padding: 0 0 9px 0;
    text-align: center;
    font-size: 1.25rem;
    font-weight: 500;
    letter-spacing: 0.05rem;
}

.linkgroup__links {
    margin: auto;
    max-width: 1200px;
}

@media ( max-width: 766px ) {
  .linkgroup {
        flex-direction: column;
        flex-wrap: wrap;
  }
  .linkgroup__col1 {
        width: 100%;
  }
  .linkgroup__col2 {
    
  }
  .linkgroup__name {
        margin: 40px 20px 0px 20px;
  }
}

.grid {
    display: flex;
    flex-grow: 1;
    flex-wrap: wrap;
    align-items: left;
    position: relative;
    padding: 40px 0 0 40px;
}

.grid__item {
    width: 153px;
    margin: 0 40px 40px 0;
}

@media ( max-width: 766px ) {
    .grid {
        padding: 20px 0 0 20px;
    }
    .grid__item {
        width: 150px;
        margin: 0 20px 20px 0;
    }
}

.grid__item--plus {
    margin-left: auto;
}

.card {
    background: var( --color-card );
    display: flex;
    position: relative;
    text-align: left;
    border-radius: 5px;
    box-shadow: 0px 0px 2px 2px rgba( 0, 0, 0, 0.075 ), 0 4px 2px 2px rgba( 0, 0, 0, 0.075 );
    font-size: 0.8rem;
    transition: all 50ms cubic-bezier( 0, 0, 0.48, 0.997 );
    transform-style: preserve-3d;
}

.theme--dark .card {
    box-shadow: 0px 0px 2px 2px rgba( 0, 0, 0, 0.15 ), 0 4px 2px 2px rgba( 0, 0, 0, 0.15 );
}

.card:after {
    content: "";
    display: block;
    padding-bottom: 100%;
}

.card:hover {
    transform: translate3d( 0, -4px, 0 );
    box-shadow: 0px 0px 2px 2px rgba( 0, 0, 0, 0.035 ), 0 4px 2px 2px rgba( 0, 0, 0, 0.035 );
}

.theme--dark .card:hover {
    box-shadow: 0px 0px 2px 2px rgba( 0, 0, 0, 0.075 ), 0 4px 2px 2px rgba( 0, 0, 0, 0.075 );
}

.card__delete {
    position: absolute;
    z-index: 2;
    top: -10px;
    right: -10px;
    width: var( --unit1 );
    height: var( --unit1 );
    text-align: center;
    line-height: 100%;
    border-radius: 50%;
    background-color: var( --color-theme-4 );
    background-image: var( --icon-close );
    background-position: center;
    background-repeat: no-repeat;
    background-size: 9px;
    opacity: 0;
    pointer-events: none;
    transition: var( --transition-default );
}

.card__delete:hover {
    cursor: pointer;
}

.card:hover .card__delete {
    opacity: 1;
    pointer-events: initial;
}

.card__inner {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-end;
    width: 100%;
    height: 100%;
    padding: 10px 10px 12px 14px;
    overflow: hidden;
}

.card__inner:after,
.card__inner:before {
    content:" ";
    width:10px;
    height:10px;
    position:absolute;
    border :0px solid #fff;
    transition:all 0.25s;
}

.card__inner:after {
    top:-1px;
    left:-1px;
    border-top:2px solid rgba( 0, 0, 0, 0 );
    border-left:2px solid rgba( 0, 0, 0, 0 );
}

.card__inner:before {
    bottom:-1px;
    right:-1px;
    border-bottom:2px solid rgba( 0, 0, 0, 0 );
    border-right:2px solid rgba( 0, 0, 0, 0 );
}

.card__inner:hover {
	border-top-right-radius:0px;
	border-bottom-left-radius:0px;
}

.card__inner:hover:before,
.card__inner:hover:after {
    width:100%;
    height:100%;
    border-color: rgba( 0, 0, 0, 0.95 );
}

.theme--dark .card__inner:hover:before,
.theme--dark .card__inner:hover:after {
    width:100%;
    height:100%;
    border-color: rgba( 255, 255, 255, 0.4 );
}

.card__favicon {
    padding-bottom: 8px;
    transition: 0.2s;
    position: absolute;
    top: 15px;
}

.card__title {
    padding-bottom: 5px;
    transition: 0.2s;
    font-size: 1rem;
    line-height: 1.2;
}

.card__url {
    font-size: 0.625rem;
    line-height: 1.2;
    padding-bottom: 0px;
    transition: 0.2s;
    color: rgba( 255, 255, 255, 0.25 );
}

.theme--light .card__url {
    color: var( --color-text-default );
}

.card--plus {
    align-items: center;
    justify-content: center;
    box-shadow: initial;
    background: rgba( 0, 0, 0, 0.025 );
    color: rgba( 0, 0, 0, 0.35 );
    font-weight: 700;
}

.card--plus:hover {
    cursor: pointer;
    box-shadow: initial;
}

.theme--dark .card--plus {
    box-shadow: initial;
    background: rgba( 255, 255, 255, 0.025 );
    color: rgba( 255, 255, 255, 0.4 );
}

.card__plus span {
    display: block;
    text-align: center;
    font-size: 2rem;
    font-weight: 300;
    line-height: 100%;
    transform: translate3d( 0, -5px, 0 );
}

.button {
    background: transparent;
    border: 0;
    padding: 0;
}

.button:hover {
    cursor: pointer;
}

.button--create-link {

}

.button--create-link {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    line-height: 100%;
    text-align: center;
    color: white;
    font-size: 3rem;
    background: var( --color-theme-4 );
    transition: var( --transition-default );
}

.button--settings {
    outline: none;
    position: fixed;
    z-index: 3;
    top: 0;
    right: 0;
    padding: 40px 40px 20px 20px;
    transition: var( --transition-default );
}

.button--settings .button__inner {
    width: 60px;
    height: 60px;
    border: 0;
    background-position: center;
    background-repeat: no-repeat;
    background: var( --icon-gear );
}

.button__wrapper:hover {
    cursor: pointer;
}

.button__wrapper--create-link {
    width: 120px;
    height: 120px;
    position: fixed;
    right: 0;
    bottom: 0;
}

.button__wrapper--create-link .button--create-link {
    position: absolute;
    bottom: 40px;
    right: 40px;
}

.button__wrapper--create-link:hover .button--create-link,
.button--settings:hover {
    transform: scale( 1.5 );
}

.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var( --color-text-dark );
}

.modal__background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: var( --color-overlay );
}

.modal__inner {
    width: 90vw;
    max-width: 500px;
    height: auto;
    position: relative;
    background: white;
    border-radius: 3px;
}

.modal__title {
    text-align: center;
    padding: 20px 10px 10px 10px;
}


form {
    margin: 0;
}

form input, 
form button[type="submit"],
form select,
form textarea {
    width: 100%;
    padding: var( --unit1 );
    margin: 0 0 calc( var( --unit1 ) / 2 ) 0;
    font-size: 1rem;
    appearance: none;
    border: 1px solid ;
    border-radius: 3px;
}

.input-field:after,
input:after {
    content: ".";
    display: block;
    height: 1px;
    clear: both;
    visibility: hidden;
}

.form {
    padding: var( --unit1 );
    margin-bottom: 0;
}

.form input {
    
}

.form--link {

}

.form--link label {
    display: none;
}

.form--link button {
    width: 100%;
    padding: var( --unit1 );
    background: var( --color-theme-4 );
    border: 0;
    color: var( --color-text-light );
}

.form--link-group button {
    background: var( --color-theme-2 );
}

.form--link-group button:hover {
    cursor: pointer;
}

.u-centered {
    text-align: center;
}

.app .firebaseui-container {
    max-width: initial;
}

.app .firebaseui-card-content {
    padding: 0;
}

.app .firebaseui-idp-google {
    max-width: initial;
    background-color: var( --color-theme-2 ) !important;
    height: 60px;
    text-align: center;
}

.app .firebaseui-idp-icon-wrapper {
    display: inline-block;
}

.app .firebaseui-idp-list {
    margin: 0;
}

.app .firebaseui-idp-icon-wrapper:after {
    content: 'Sign In and Create Your Quicklinks';
    padding: 0 0 0 16px;
    display: inline-block;
    transform: translate3d( 0, 1px, 0 );
}

.app .firebaseui-idp-text {
    display: none !important;
}

.sidebar {
    position: fixed;
    z-index: 2;
    left: 100%;
    top: 0;
    width: 25vw;
    height: 100vh;
    display: block;
    transition: var( --transition-fast );
    background: var( --color-theme-2 );
}

.sidebar__overlay {
    width: 100vw;
    height: 100vh;
    position: fixed;
    z-index: 1;
    top: 0;
    left: -100vw;
    background: var( --color-overlay );
}

.sidebar-enter-active,
.sidebar-enter-done {
    transform: translate3d( -25vw, 0, 0 );
}

@media ( max-width: 766px ) {
    .sidebar {
        width: 80vw;
    }
    .sidebar-enter-active,
    .sidebar-enter-done,
    .sidebar.sidebar-enter-active ~ main,
    .sidebar.sidebar-enter-done ~ main {
        transform: translate3d( -80vw, 0, 0 );
    }
}

.toggle {
    display: flex;
}

.toggle .toggle__checkbox {
    display: none;
}

.toggle .toggle__label {
    outline: 0;
    display: block;
    width: 4em;
    height: 2em;
    position: relative;
    cursor: pointer;
    user-select: none;
    background: #f0f0f0;
    border-radius: 2em;
    padding: 2px;
    transition: all .4s ease;
    outline: none;
}

.toggle .toggle__label:after,
.toggle .toggle__label:before {
    position: relative;
    display: block;
    content: "";
    width: 50%;
    height: 100%;
}

.toggle .toggle__label:after {
    left: 0;
    border-radius: 50%;
    background: #fff;
    transition: all .2s ease;
}

.toggle .toggle__label:before {
    display: none;
}

.toggle .toggle__checkbox:checked + .toggle__label {
    background: #9FD6AE;
}

.toggle .toggle__checkbox:checked + .toggle__label:after {
    left: 50%;
}